import { IDictionary } from './types';

export interface IResourceManager {
    /**
     * @function    {getModuleResources}    - Method to fetch resource strings of a module for given locale
     * @param       {moduleName}            - name of the module
     * @param       {locale}                - locale for which resource strings need to be fetched
     */
    getModuleResources(moduleName: string, themeName: string, locale: string): IDictionary<string>;

    /**
     * @function    {getAuthoringResources} - Method to fetch authoring related resource strings for all locales
     */
    getAuthoringResources(): IDictionary<IAuthoringResourceDefinition>;
}

export interface IResourceProperty {
    value: string;
    comment: string;
}

export interface IModuleAuthoringResourceDefinition {
    config: IDictionary<IResourceProperty>;
    description: IResourceProperty;
    editorGroups: IDictionary<IResourceProperty>;
    options: IDictionary<IResourceProperty>;
    friendlyName: IResourceProperty;
    images: IDictionary<IResourceProperty>;
    slots: IDictionary<IResourceProperty>;
    tags: IDictionary<IResourceProperty>;
}

export interface IStylePresetAuthoringResourceDefinition {
    name: IResourceProperty;
    description: IResourceProperty;
    global: IDictionary<IResourceProperty>;
    modules: IDictionary<IResourceProperty>;
}

export interface IStylePresetAuthoringStylesResources {
    definition: IStylePresetAuthoringResourceDefinition;
    presets: IDictionary<IStylePresetAuthoringResourceDefinition>;
}

export interface IThemeAuthoringResourceDefinition {
    name: IResourceProperty;
    description: IResourceProperty;
    styles: IStylePresetAuthoringStylesResources;
}

export interface IAuthoringResourceDefinition {
    modules: IDictionary<IModuleAuthoringResourceDefinition>;
    settings: {
        config: IDictionary<IResourceProperty>;
        routes: IDictionary<IResourceProperty>;
    };
    themes: IDictionary<IThemeAuthoringResourceDefinition>;
}
